:root {
  --main-font-family: "DM Sans", sans-serif;
  --secondary-font-family: "Titan One", sans-serif;
  --color-primary-dark: #000;
  --color-primary-grey: #907e82;
  --color-primary-light: #fff;
  --color-secondary-light-pink: #ffa5ba;
  --color-secondary-dark-pink: #d41443;
  --color-repeating-linear-gradient: #e1e1e1;
  --color-icon-customer: #c7133e;
  --color-icon-customer-primary: #d9d9d9;
  --bg-color-white: #fff;
  --bg-color-light-pink: #fff5f6;
  --bg-color-dark-pink: #ffb8ca;
  --bg-color-mobile-menu: #e17992;
  --bg-color-customer: #fafafa;
  --footer-icon-prim: #fea5bb;
  --cubic-transition-scale: scale .4s cubic-bezier(.4, 0, .2, 1);
  --cubic-transition-scale--delay: scale .4s cubic-bezier(.4, 0, .2, 1) .3s;
  --cubic-transition-scale--small-delay: scale .4s cubic-bezier(.4, 0, .2, 1) .1s;
  --cubic-transition-translate--delay: translate .4s cubic-bezier(.4, 0, .2, 1) .3s;
  --cubic-transition-translate: translate .4s cubic-bezier(.4, 0, .2, 1);
  --cubic-transition-width: width .4s cubic-bezier(.4, 0, .2, 1);
  --cubic-transition-transfrom: transfrom .4s cubic-bezier(.4, 0, .2, 1);
  --cubic-transition-rotateX-in: rotateX-in .3s ease-in-out;
}

.container {
  width: 100%;
  max-width: 480px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 20px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 35px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
    padding: 0 115px;
  }
}

.page-header {
  background-color: var(--bg-color-dark-pink);
}

.page-header__circle-wrapper {
  width: 100%;
  position: absolute;
  overflow: hidden;
}

.page-header__circle {
  width: 346px;
  height: 344px;
  z-index: 0;
  background-color: var(--color-secondary-light-pink);
  border-radius: 50%;
  margin-top: 83px;
  margin-left: 36px;
  animation-name: changeBgColor;
  animation-duration: 2.5s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  display: inline-block;
  box-shadow: 10px 0 10px -5px #d414434d, -10px 0 10px -5px #d414434d;
}

@keyframes changeBgColor {
  0% {
    background-color: #d4144333;
  }

  100% {
    background-color: var(--color-secondary-light-pink);
  }
}

@media screen and (min-width: 768px) {
  .page-header__circle {
    width: 324px;
    height: 322px;
    margin-top: 11px;
    margin-left: 256px;
  }
}

@media screen and (min-width: 1280px) {
  .page-header__circle {
    width: 538px;
    height: 538px;
    margin-top: 19px;
    margin-left: 333px;
  }
}

