.header-statistics {
  display: none;
}

@media screen and (min-width: 768px) {
  .header-statistics {
    flex-direction: column;
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
  }
}

.header-statistics__element:first-child {
  margin-bottom: 13px;
}

.header-statistics__number {
  z-index: 99999;
  font-family: var(--secondary-font-family);
  text-align: right;
  letter-spacing: .04em;
  text-transform: uppercase;
  color: var(--color-secondary-dark-pink);
  font-size: 28px;
  line-height: 32px;
  position: relative;
}

.header-statistics__number:hover {
  transition: transform .5s linear;
  transform: scale(1.4);
}

.header-statistics__number:after {
  z-index: -1;
  content: "";
  width: 16px;
  height: 16px;
  background-color: var(--color-primary-light);
  border-radius: 50%;
  animation: .6s ease-in-out infinite alternate dots;
  display: inline-block;
  position: absolute;
  top: -3px;
  right: -7px;
}

@keyframes dots {
  100% {
    transform: scale(1.2);
  }
}

@media screen and (min-width: 1280px) {
  .header-statistics__number {
    font-size: 46px;
    font-weight: 400;
    line-height: 53px;
  }

  .header-statistics__number:after {
    width: 30px;
    height: 30px;
    top: -3px;
    right: -18px;
  }
}

.header-statistics__label {
  font-family: var(--main-font-family);
  text-align: right;
  color: var(--color-primary-light);
  font-size: 8px;
  line-height: 10px;
}

@media screen and (min-width: 1280px) {
  .header-statistics__label {
    font-size: 14px;
    line-height: 18px;
  }
}

