.menu-container {
  width: 100vw;
  height: 100vh;
  background: var(--bg-color-mobile-menu);
  z-index: 999;
  padding: 48px 40px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1), fill .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(100%);
  box-shadow: 0 4px 8px #0000000a, 0 2px 4px #00000014, 0 1px 3px #00000029;
}

.menu-container.is-open {
  transform: translateX(0);
}

.menu-container .mobile-menu-closing-icon {
  stroke: var(--color-primary-light);
  background-color: #0000;
  position: absolute;
  top: 23px;
  right: 32px;
}

